body{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: hsl(142, 85%, 95%);
}

.stopwatch{
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px solid rgb(114, 114, 114);
  border-radius: 50px;
  background-color: hsl(130, 85%, 92%);
  padding: 30px;
}

.display{
  font-size: 5rem;
  font-family: monospace;
  font-weight: bold;
  color: hsl(0, 0%, 30%);
  text-shadow: 2px 2px 2px hsla(0, 0%, 0%, 0.75);
  margin-bottom: 25px;
}

.controls button{
  font-size: 1.5rem;
  font-weight: bold;
  padding: 10px 20px;
  margin: 5px;
  min-width: 125px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  color: white;
  transition: background-color 0.5s ease;
}

.start-button{
  background-color: hsl(115, 100%, 40%);
}
.start-button:hover{
  background-color: hsl(115, 100%, 35%);
}

.stop-button{
  background-color: hsl(10, 90%, 50%);
}
.stop-button:hover{
  background-color: hsl(10, 90%, 45%);
}

.reset-button{
  background-color: hsl(205, 90%, 60%);
}
.reset-button:hover{
  background-color: hsl(205, 90%, 55%);
}
